<template>
  <ul class="subjective__tem__wrap vertical__scroll">
    <li>
      <dl>
        <dt>대표질문</dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            ref="questionInput1"
            v-model="paramObj.representativeQuestion"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>힌트</dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            ref="questionHintInput"
            v-model="paramObj.hint"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>정답수</dt>
        <dd>
          <select
            name=""
            id=""
            ref="answerSizeSelctor"
            @change="answerSizeChange($event.target.value)"
            v-model="paramObj.answerCnt"
            :disabled="isPreview"
          >
            <option
              :value="questionSize"
              v-for="questionSize in totalQuestionLimit"
              :key="questionSize"
            >
              {{ questionSize }}
            </option>
          </select>
        </dd>
      </dl>
    </li>
    <li
      class="question__list double__dash"
      v-for="(list, idx) in answerInputArr"
      :key="idx"
    >
      <dl>
        <dt>질문 {{ idx + 1 }}</dt>
        <dd class="question__input">
          <textarea
            name=""
            id=""
            :value="list.question"
            @keyup="changeQuestion($event.target.value, idx)"
            class="custom__input"
            :disabled="isPreview"
          ></textarea>
        </dd>
      </dl>
      <dl>
        <dt>정답 {{ idx + 1 }}</dt>
        <dd>
          <input
            @keyup="changeAnswer($event.target.value, idx)"
            type="text"
            :value="list.answer"
            class="custom__input"
            :disabled="isPreview"
          />
        </dd>
      </dl>
      <dl v-if="list.hasOwnProperty('userAnswer')">
        <dt>답변 {{ idx + 1 }}</dt>
        <dd>
          <input
            type="text"
            :disabled="isPreview"
            :value="list.userAnswer"
            class="custom__input read__only"
          />
        </dd>
      </dl>
    </li>
  </ul>
</template>
<script>
export default {
  data() {
    return {
      paramObj: {
        representativeQuestion: "",
        question: "",
        answer: "",
        answerCnt: 1,
        hint: "",
      },
      answerInputArr: [{ question: "", answer: "" }],
      //answerInputArr: [],
      totalQuestionLimit: 6,
    };
  },
  props: {
    updateQuestionInfo: [String, Object, Array],
    isPreview: Boolean,
  },
  created() {
    if (typeof this.updateQuestionInfo === "object") {
      if (!Array.isArray(this.updateQuestionInfo)) {
        if (
          this.updateQuestionInfo.typicalQuestion !== "" &&
          this.updateQuestionInfo !== ""
        ) {
          this.paramObj.representativeQuestion =
            this.updateQuestionInfo.typicalQuestion;
          this.paramObj.answerCnt = this.updateQuestionInfo.questions.length;
          this.answerInputArr = [];
          for (let i = 0; i < this.paramObj.answerCnt; i++) {
            this.answerInputArr.push({
              question: this.updateQuestionInfo.questions[i].question,
              answer: this.updateQuestionInfo.questions[i].answer,
            });
          }
          this.paramObj.question =
            this.updateQuestionInfo.questions[0].question;
          this.paramObj.answer = this.updateQuestionInfo.questions[0].answer;
          this.paramObj.hint = this.updateQuestionInfo.hint;
        }
      } else {
        if (
          this.updateQuestionInfo[0].typicalQuestion !== "" &&
          this.updateQuestionInfo[0] !== ""
        ) {
          this.paramObj.representativeQuestion =
            this.updateQuestionInfo[0].typicalQuestion;
          this.paramObj.answerCnt = this.updateQuestionInfo[0].questions.length;
          this.answerInputArr = [];
          for (let i = 0; i < this.paramObj.answerCnt; i++) {
            this.answerInputArr.push(this.updateQuestionInfo[0].questions[i]);
          }
          this.paramObj.question =
            this.updateQuestionInfo[0].questions[0].question;
          this.paramObj.answer = this.updateQuestionInfo[0].questions[0].answer;
          this.paramObj.hint = this.updateQuestionInfo[0].hint;
        }
      }
    } else {
      this.answerSizeChange(1);
    }
  },
  methods: {
    answerSizeChange(val) {
      this.answerInputArr = [];

      for (let i = 0; i < val; i++) {
        this.answerInputArr.push({ question: "", answer: "" });
      }
    },
    changeQuestion(val, idx) {
      this.answerInputArr[idx].question = val;
    },
    changeAnswer(val, idx) {
      this.answerInputArr[idx].answer = val;
    },
    saveBtnClick() {
      const paramObj = {
        representativeQuestion: this.$refs.questionInput1.value,
        questionArr: this.answerInputArr,
        hint: this.$refs.questionHintInput.value,
      };
      this.$emit("templateInputObj", paramObj);
    },
    initData() {
      this.paramObj = {
        representativeQuestion: "",
        question: "",
        answer: "",
        answerCnt: 1,
        hint: "",
      };
      this.answerInputArr = [{ question: "", answer: "" }];
    },
  },
};
</script>
