<template>
  <ul class="video__tem__wrap vertical__scroll">
    <li>
      <dl>
        <dt>대표질문</dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            ref="questionInput"
            v-model="paramObj.representativeQuestion"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>힌트</dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            ref="questionHintInput"
            v-model="paramObj.hint"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>질문</dt>
        <dd class="question__input">
          <textarea
            name=""
            id=""
            class="custom__input"
            ref="answerInput"
            v-model="paramObj.question"
            :disabled="isPreview"
          ></textarea>
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>정답</dt>
        <dd>
          <input
            type="text"
            class="custom__input read__only"
            readonly
            value="동영상 끝까지 시청시, 정답"
          />
        </dd>
      </dl>
    </li>
  </ul>
</template>
<script>
export default {
  data() {
    return {
      paramObj: {
        representativeQuestion: "",
        question: "",
        hint: "",
      },
    };
  },
  props: {
    updateQuestionInfo: [Object, String, Array],
    addMeaicineType: String,
    isPreview: Boolean,
  },
  watch: {
    addMeaicineType() {
      this.$refs.questionInput.value = "";
      this.$refs.answerInput.value = "";
    },
  },
  created() {
    if (typeof this.updateQuestionInfo === "object") {
      if (!Array.isArray(this.updateQuestionInfo)) {
        if (
          this.updateQuestionInfo.typicalQuestion !== "" &&
          this.updateQuestionInfo !== ""
        ) {
          this.paramObj.representativeQuestion =
            this.updateQuestionInfo.typicalQuestion;
          this.paramObj.question = this.updateQuestionInfo.questions[0].answer;
          this.paramObj.hint = this.updateQuestionInfo.hint;
        }
      } else {
        if (
          this.updateQuestionInfo[0].typicalQuestion !== "" &&
          this.updateQuestionInfo[0] !== ""
        ) {
          this.paramObj.representativeQuestion =
            this.updateQuestionInfo[0].typicalQuestion;
          this.paramObj.question =
            this.updateQuestionInfo[0].questions[0].answer;
          this.paramObj.hint = this.updateQuestionInfo[0].hint;
        }
      }
    }
  },
  methods: {
    saveBtnClick() {
      const paramObj = {
        representativeQuestion: this.$refs.questionInput.value,
        answer: this.$refs.answerInput.value,
        hint: this.$refs.questionHintInput.value,
      };
      this.$emit("templateInputObj", paramObj);
    },
    initData() {
      this.paramObj = {
        representativeQuestion: "",
        question: "",
        hint: "",
      };
    },
  },
};
</script>
