<template>
  <ul class="multiplechoice__tem__wrap vertical__scroll">
    <li>
      <dl>
        <dt>대표질문</dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            ref="questionInput1"
            v-model="paramObj.representativeQuestion"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>힌트</dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            ref="questionHintInput"
            v-model="paramObj.hint"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>질문</dt>
        <dd class="question__input">
          <textarea
            name=""
            id=""
            class="custom__input"
            ref="questionInput2"
            v-model="paramObj.question"
            :disabled="isPreview"
          ></textarea>
        </dd>
      </dl>
    </li>
    <li class="double__dash">
      <dl>
        <dt>선택수</dt>
        <dd>
          <select
            name=""
            id=""
            @change="selectSizeChange($event.target.value)"
            ref="selectSizeSelector"
            v-model="paramObj.choiceCnt"
            :disabled="isPreview"
          >
            <option
              :selected="selectSize === 4"
              :value="selectSize"
              v-for="selectSize in totalQuestionLimit"
              :key="selectSize"
            >
              {{ selectSize }}
            </option>
          </select>
        </dd>
      </dl>
    </li>
    <li v-for="(obj, idx) in selectInputArr" :key="idx">
      <dl>
        <dt>선택 {{ idx + 1 }}</dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            :value="typeof obj === 'string' ? obj : obj.choice"
            @keyup="changeAnswer($event.target.value, idx)"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
    <li class="double__dash">
      <dl>
        <dt>정답수</dt>
        <dd>
          <select
            name=""
            id=""
            @change="answerSizeChange($event.target.value)"
            ref="answerSizeSelector"
            v-model="paramObj.answer"
            :disabled="isPreview"
          >
            <option
              :value="size"
              v-for="size in selectInputArr.length"
              :key="size"
            >
              {{ size }}
            </option>
          </select>
        </dd>
      </dl>
    </li>
    <li
      v-for="(answerSize, idx) in answerSizeArr.length"
      :key="answerSize.answerNumber"
    >
      <dl>
        <dt>정답 {{ answerSize }}</dt>
        <dd>
          <select
            name=""
            id=""
            @change="singleAnswerChange($event.target.value, idx)"
            :disabled="isPreview"
            v-model="answerSizeArr[idx].answerNumber"
          >
            <option
              :value="answer"
              v-for="answer in selectInputArr.length"
              :key="answer"
            >
              {{ answer }}
            </option>
          </select>
        </dd>
      </dl>
    </li>
  </ul>
</template>
<script>
export default {
  data() {
    return {
      paramObj: {
        representativeQuestion: "",
        question: "",
        answer: 1,
        choiceCnt: 4,
        hint: "",
      },
      selectInputArr: [
        { choice: "" },
        { choice: "" },
        { choice: "" },
        { choice: "" },
      ],
      answerSizeArr: [{ answerNumber: 1 }],
      //answerSizeArr: [],
      totalQuestionLimit: 6,
    };
  },
  props: {
    updateQuestionInfo: [Object, String, Array],
    isPreview: Boolean,
  },
  created() {
    console.log(this.isPreview);
    if (this.$env == "development") console.log(this.updateQuestionInfo);
    if (typeof this.updateQuestionInfo === "object") {
      if (!Array.isArray(this.updateQuestionInfo)) {
        if (
          this.updateQuestionInfo.typicalQuestion !== "" &&
          this.updateQuestionInfo !== ""
        ) {
          // 대표 질문
          this.paramObj.representativeQuestion =
            this.updateQuestionInfo.typicalQuestion;
          // 질문
          this.paramObj.question =
            this.updateQuestionInfo.questions[0].question;
          // 정답 수
          const answerArray =
            this.updateQuestionInfo.questions[0].answer.split(",");
          if (this.$env == "development") console.log(answerArray);
          this.paramObj.answer = answerArray.length;
          // 선택 수
          this.paramObj.choiceCnt =
            this.updateQuestionInfo.questions[0].choices.length;
          this.selectInputArr = [];
          for (let i = 0; i < this.paramObj.choiceCnt; i++) {
            this.selectInputArr[i] = {
              choice: this.updateQuestionInfo.questions[0].choices[i],
            };
          }
          this.answerSizeArr = [];
          for (let i = 0; i < answerArray.length; i++) {
            this.answerSizeArr.push({ answerNumber: answerArray[i] });
          }
          // 힌트
          this.paramObj.hint = this.updateQuestionInfo.hint;
        }
      } else {
        if (
          this.updateQuestionInfo[0].typicalQuestion !== "" &&
          this.updateQuestionInfo[0] !== ""
        ) {
          // 대표 질문
          this.paramObj.representativeQuestion =
            this.updateQuestionInfo[0].typicalQuestion;
          // 질문
          this.paramObj.question =
            this.updateQuestionInfo[0].questions[0].question;
          // 정답 수
          const answerArray =
            this.updateQuestionInfo[0].questions[0].answer.split(",");
          if (this.$env == "development") console.log(answerArray);
          this.paramObj.answer = answerArray.length;
          // 선택 수
          this.paramObj.choiceCnt =
            this.updateQuestionInfo[0].questions[0].choices.length;
          this.selectInputArr = [];
          for (let i = 0; i < this.paramObj.choiceCnt; i++) {
            this.selectInputArr[i] =
              this.updateQuestionInfo[0].questions[0].choices[i];
          }
          this.answerSizeArr = [];
          for (let i = 0; i < answerArray.length; i++) {
            this.answerSizeArr.push({ answerNumber: answerArray[i] });
          }
          // 힌트
          this.paramObj.hint = this.updateQuestionInfo[0].hint;
        }
      }
    }
  },
  methods: {
    selectSizeChange(val) {
      this.selectInputArr = [];
      for (let i = 0; i < val; i++) {
        this.selectInputArr.push({ choice: "" });
      }
    },
    answerSizeChange(val) {
      this.answerSizeArr = [];
      for (let i = 0; i < val; i++) {
        this.answerSizeArr.push({ answerNumber: 1 });
      }
    },
    changeAnswer(val, idx) {
      this.selectInputArr[idx].choice = val;
    },
    singleAnswerChange(val, idx) {
      this.answerSizeArr[idx].answerNumber = val;
    },
    saveBtnClick() {
      const paramObj = {
        representativeQuestion: this.$refs.questionInput1.value,
        question: this.$refs.questionInput2.value,
        selectInputArr: this.selectInputArr,
        answerSizeArr: this.answerSizeArr,
        hint: this.$refs.questionHintInput.value,
      };
      this.$emit("templateInputObj", paramObj);
    },
    initData() {
      this.paramObj = {
        representativeQuestion: "",
        question: "",
        answer: 1,
        choiceCnt: 4,
        hint: "",
      };
      this.selectInputArr = [
        { choice: "" },
        { choice: "" },
        { choice: "" },
        { choice: "" },
      ];
      this.answerSizeArr = [{ answerNumber: 1 }];
    },
  },
};
</script>
