<template>
  <ul class="word__recal__tem__wrap vertical__scroll">
    <li>
      <dl>
        <dt>단어 리스트</dt>
        <dd class="csv__input__list">
          <input
            type="text"
            class="custom__input"
            ref="cvsInput"
            v-model="csvInputHint"
            disabled="true"
          />
          <div>
            <label>
              <a
                href="https://ubidtx01.kr.object.ncloudstorage.com/ubi01/word_recall_sample.csv"
                >샘플파일 다운로드</a
              >
            </label>
            <label for="csvInput" v-if="!isPreview">
              <img
                src="@/assets/images/icon/ic-plus.svg"
                alt="파일 추가 아이콘"
              />
              <p>파일 추가</p>
            </label>
          </div>
          <input
            class="file"
            @change="changeFile($event.target)"
            type="file"
            ref="csvInput"
            id="csvInput"
          />
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>단어</dt>
        <dd class="question__input">
          <textarea
            class="csv__word__list"
            ref="wordInput"
            v-model="paramObj.wordList"
            disabled="true"
          ></textarea>
        </dd>
      </dl>
    </li>
  </ul>
</template>
<script>
export default {
  data() {
    return {
      paramObj: {
        wordList: "",
      },
      csvInputHint: "콤마로 구분된 csv 파일을 등록해주세요.",
    };
  },
  props: {
    updateQuestionInfo: [Object, String, Array],
    isPreview: Boolean,
  },
  created() {
    if (typeof this.updateQuestionInfo === "object") {
      if (!Array.isArray(this.updateQuestionInfo)) {
        if (
          this.updateQuestionInfo.typicalQuestion !== "" &&
          this.updateQuestionInfo !== ""
        ) {
          this.paramObj.wordList = this.updateQuestionInfo.typicalQuestion;
        }
      } else {
        if (
          this.updateQuestionInfo[0].typicalQuestion !== "" &&
          this.updateQuestionInfo[0] !== ""
        ) {
          this.paramObj.wordList = this.updateQuestionInfo[0].typicalQuestion;
        }
      }
    }
  },
  methods: {
    saveBtnClick() {
      const paramObj = {
        representativeQuestion: this.paramObj.wordList,
        answer: "",
      };
      this.$emit("templateInputObj", paramObj);
    },
    changeFile(param) {
      if (this.$env == "development") console.log(param);
      const vm = this;
      if (param.files.length > 0) {
        let cvsFile = param.files[0];
        const reader = new FileReader();
        reader.readAsText(cvsFile);
        reader.onload = function () {
          vm.paramObj.wordList = this.result;
        };
      }
    },
    initData() {
      this.paramObj = {
        wordList: "",
      };
    },
  },
};
</script>
