<template>
  <ul class="adl__tem__wrap vertical__scroll">
    <li>
      <dl>
        <dt>ADL 선택</dt>
        <dd>
          <select name="" id="" v-model="selectedLink">
            <option value="http://dev.mebonsoft.com/adl/recycle.html">
              분리수거
            </option>
            <option value="http://dev.mebonsoft.com/adl/kiosk.html">
              키오스크
            </option>
            <option value="http://dev.mebonsoft.com/adl/atm.html">
              ATM기 사용
            </option>
          </select>
          <button class="custom__btn" @click="previewBtnClick">
            <p>미리보기</p>
          </button>
        </dd>
      </dl>
    </li>
  </ul>
</template>
<script>
export default {
  data() {
    return {
      selectedLink: "",
    };
  },
  props: {
    updateQuestionInfo: [Object, String, Array],
    isPreview: Boolean,
  },
  created() {
    if (typeof this.updateQuestionInfo === "object") {
      if (!Array.isArray(this.updateQuestionInfo)) {
        if (this.updateQuestionInfo.url !== "") {
          this.selectedLink = this.updateQuestionInfo.url;
        }
      } else {
        if (this.updateQuestionInfo[0].url !== "") {
          this.selectedLink = this.updateQuestionInfo.url;
        }
      }
    }
  },
  methods: {
    previewBtnClick() {
      const url = this.selectedLink;
      if (url === "") {
        this.$confirm({
          // title: "",
          message: "ADL 타입을 선택해주세요.",
          button: {
            yes: "확인",
          },
        });
      } else {
        const name = "ADL 페이지";
        const option = "width = 380, height = 660, top = 100, left = 200";
        window.open(url, name, option);
      }
    },
    saveBtnClick() {
      const paramObj = {
        linkInput: this.selectedLink,
      };
      this.$emit("templateInputObj", paramObj);
    },
    initData() {
      this.selectedLink = "";
    },
  },
};
</script>
