<template>
  <ul class="oxquiz__tem__wrap vertical__scroll">
    <li>
      <dl>
        <dt>대표질문</dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            ref="questionInput1"
            v-model="paramObj.representativeQuestion"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>힌트</dt>
        <dd>
          <input
            type="text"
            class="custom__input"
            ref="questionHintInput"
            v-model="paramObj.hint"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>질문</dt>
        <dd class="question__input">
          <textarea
            name=""
            id=""
            class="custom__input"
            ref="questionInput2"
            v-model="paramObj.question"
            :disabled="isPreview"
          ></textarea>
        </dd>
      </dl>
    </li>
    <li>
      <dl>
        <dt>정답</dt>
        <dd>
          <select
            name=""
            id=""
            @change="changeOX($event.target.value)"
            v-model="paramObj.answer"
            :disabled="isPreview"
          >
            <option value="O">O</option>
            <option value="X">X</option>
          </select>
        </dd>
      </dl>
    </li>
    <li v-if="paramObj.hasOwnProperty('userAnswer')">
      <dl>
        <dt>답변</dt>
        <dd>
          <input
            type="text"
            :value="paramObj.userAnswer"
            class="custom__input"
            :disabled="isPreview"
          />
        </dd>
      </dl>
    </li>
  </ul>
</template>
<script>
export default {
  data() {
    return {
      paramObj: {
        representativeQuestion: "",
        question: "",
        answer: "O",
        hint: "",
      },
    };
  },
  props: {
    updateQuestionInfo: [Object, String, Array],
    isPreview: Boolean,
  },
  created() {
    if (typeof this.updateQuestionInfo === "object") {
      if (!Array.isArray(this.updateQuestionInfo)) {
        if (
          this.updateQuestionInfo.typicalQuestion !== "" &&
          this.updateQuestionInfo !== ""
        ) {
          this.paramObj.representativeQuestion =
            this.updateQuestionInfo.typicalQuestion;
          this.paramObj.question =
            this.updateQuestionInfo.questions[0].question;
          this.paramObj.answer = this.updateQuestionInfo.questions[0].answer;
          this.paramObj.hint = this.updateQuestionInfo.hint;
        }
      } else {
        if (
          this.updateQuestionInfo[0].typicalQuestion !== "" &&
          this.updateQuestionInfo[0] !== ""
        ) {
          this.paramObj.representativeQuestion =
            this.updateQuestionInfo[0].typicalQuestion;
          this.paramObj.hint = this.updateQuestionInfo[0].hint;
          this.paramObj.question =
            this.updateQuestionInfo[0].questions[0].question;
          this.paramObj.answer = this.updateQuestionInfo[0].questions[0].answer;
          if (
            Object.prototype.hasOwnProperty.call(
              this.updateQuestionInfo[0].questions[0],
              "userAnswer"
            )
          ) {
            this.paramObj.userAnswer =
              this.updateQuestionInfo[0].questions[0].userAnswer;
          }
        }
      }
    }
  },
  methods: {
    saveBtnClick() {
      this.paramObj.representativeQuestion = this.$refs.questionInput1.value;
      this.paramObj.question = this.$refs.questionInput2.value;
      this.paramObj.hint = this.$refs.questionHintInput.value;
      this.$emit("templateInputObj", this.paramObj);
    },
    changeOX(val) {
      this.paramObj.answer = val;
    },
    initData() {
      this.paramObj = {
        representativeQuestion: "",
        question: "",
        answer: "O",
        hint: "",
      };
    },
  },
};
</script>
